body{margin:0;
  padding:0;
  background:#f2f4f5;
  color:#1e1e1f;
  font-family:'montserrat',Arial,Helvetica,sans-serif
}

#root{
  margin:0 24px;
  position:relative;
  min-height:100vh
}
.Modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6); /* Fondo difuminado */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .Modal-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px; /* Bordes redondeados */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Sombra */
    max-width: 500px;
    width: 100%;
    z-index: 1001;
    position: relative;
    animation: slide-in 0.3s ease-out; /* Animación de entrada */
  }
  
  /* Animación de entrada */
  @keyframes slide-in {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  } 